import React, { Component } from 'react';
import { Media } from 'reactstrap';
import Slider from "react-slick";

import './artists.css';
import i12 from '../../assets/img/12.svg';

export default class Artists extends Component {
    render() {
        const artists = [
            {
                img: 'https://firebasestorage.googleapis.com/v0/b/juz-entertainment-8c282.appspot.com/o/az-gray-min.jpg?alt=media&token=810da869-8175-4b9b-b86e-db0800308d64',
                name: 'A.Z',
                full_name: 'Азамат Зенкаев',
                position: 'Лидер группы Ninety One, рэпер'
            },
            {
                img: 'https://firebasestorage.googleapis.com/v0/b/juz-entertainment-8c282.appspot.com/o/alem-gray-min.jpg?alt=media&token=ba928d3f-e947-45f3-a473-6f1cf6ef430f',
                name: 'Alem',
                full_name: 'Батырхан Маликов',
                position: 'Главный вокалист Ninety One'
            },
            {
                img: 'https://firebasestorage.googleapis.com/v0/b/juz-entertainment-8c282.appspot.com/o/bala-gray-min.jpg?alt=media&token=999962b9-943a-49e8-9b76-9e3da3d19325',
                name: 'Bala',
                full_name: 'Данияр Кулумшин',
                position: 'Лидирующий вокалист Ninety One'
            },
            {
                img: 'https://firebasestorage.googleapis.com/v0/b/juz-entertainment-8c282.appspot.com/o/az-gray-min.jpg?alt=media&token=810da869-8175-4b9b-b86e-db0800308d64',
                name: 'A.Z',
                full_name: 'Азамат Зенкаев',
                position: 'Лидер группы Ninety One, рэпер'
            },
            {
                img: 'https://firebasestorage.googleapis.com/v0/b/juz-entertainment-8c282.appspot.com/o/alem-gray-min.jpg?alt=media&token=ba928d3f-e947-45f3-a473-6f1cf6ef430f',
                name: 'Alem',
                full_name: 'Батырхан Маликов',
                position: 'Главный вокалист Ninety One'
            },
            {
                img: 'https://firebasestorage.googleapis.com/v0/b/juz-entertainment-8c282.appspot.com/o/bala-gray-min.jpg?alt=media&token=999962b9-943a-49e8-9b76-9e3da3d19325',
                name: 'Bala',
                full_name: 'Данияр Кулумшин',
                position: 'Лидирующий вокалист Ninety One'
            }
        ];
        const settings = {
            infinite: true,
            centerMode: true,
            slidesToShow: 3,
            centerPadding: '0',
            slidesToScroll: 1,
            vertical: true,
            verticalSwiping: true,
            focusOnSelect: true,
            responsive: [
                {
                    breakpoint:  992,
                    settings: {
                        vertical: false,
                        verticalSwiping: false,
                        centerMode: true,
                        variableWidth: true,
                        centerPadding: '25px',
                        slidesToShow: 4,
                    }
                },
                {
                    breakpoint:  768,
                    settings: {
                        vertical: false,
                        verticalSwiping: false,
                        centerMode: true,
                        variableWidth: true,
                        centerPadding: '25px',
                        slidesToShow: 3,
                    }
                },
                {
                    breakpoint:  576,
                    settings: {
                        vertical: false,
                        verticalSwiping: false,
                        centerMode: true,
                        variableWidth: true,
                        centerPadding: '25px',
                        slidesToShow: 2,
                    }
                },
                {
                    breakpoint:  360,
                    settings: {
                        vertical: false,
                        verticalSwiping: false,
                        centerMode: true,
                        variableWidth: true,
                        centerPadding: '25px',
                        slidesToShow: 1,
                    }
                }
            ]
        };

        const getArtists = () => {
            let rows = [];
            for (let i in artists) {
                const artist = artists[i];
                rows.push(
                    <div key={i}>
                        <div className="artist-item align-items-center d-block d-lg-flex">
                            <div className="artist-photo">
                                <div className="image-container mr-50" title={artist.name}>
                                    <img src={artist.img} alt=""/>
                                </div>
                            </div>
                            <div className="artist-info">
                                <h3 className="font-24">
                                    <span className="text-uppercase"> { artist.name } </span>
                                    <br/>
                                    { artist.full_name }
                                </h3>
                                <p className="o-75">
                                    { artist.position }
                                </p>
                            </div>
                        </div>
                    </div>
                )
            }
            return rows;
        };
        return (
            <div className="artists-page" >
                <section className="bg-gray bg-lined">
                    <div className="artists-bg-image" style={{backgroundImage: `url(${i12})`}}/>
                    <div className="container" >
                        <div className="row align-items-center">
                            <div className="col-lg-5 artists-jumbotron">
                                <h1 className="mb-20">
                                    <span className="text-uppercase">
                                        АРТИСТЫ JUZ ENT
                                    </span>
                                </h1>
                                <p className="big" style={{maxWidth: '348px'}}>
                                    Продюсер: Участник казахстанской музыкальной группы ORDA - Ерболат Беделхан
                                </p>
                            </div>
                            <div className="col-lg-7 p-0">
                                <Slider {...settings} className="artists-list full-view">
                                    { getArtists() }
                                </Slider>
                            </div>
                        </div>
                    </div>
                </section>
            </div>
        );
    }

}


